import Button from 'react-bootstrap/Button'
import React, { useState } from 'react'
import Form from 'react-bootstrap/Form'
import emailjs from 'emailjs-com'
import Swal from 'sweetalert2'
import image1 from '../images/imagecontact.jpg'
import Col from 'react-bootstrap/Col'
import Row from 'react-bootstrap/Row'
export default function Contact() {
  const USER_ID = 'user_axlSbTw5emprxSc7Yuu1y'
  const TEMPLATE_ID = 'template_je1j9f9'
  const SERVICE_ID = 'service_f0pt9cg'

  const [lastName, setLastName] = useState('')
  const [firstName, setFirstName] = useState('')
  const [phoneNumber, setPhoneNumber] = useState(0)
  const [email, setEmail] = useState('')

  const contactValidation = () => {}

  const handleOnSubmit = (e) => {
    e.preventDefault()
    emailjs.sendForm(SERVICE_ID, TEMPLATE_ID, e.target, USER_ID).then(
      (result) => {
        console.log(result.text)
        Swal.fire({
          icon: `success`,
          title: `הודעה נשלחה בהצלחה , בקרוב אצור איתך קשר`,
        })
      },
      (error) => {
        console.log(error.text)
        Swal.fire({
          icon: `error`,
          title: `Ooops, something went wrong`,
          text: error.text,
        })
      }
    )
    e.target.reset()
  }
  return (
    <div className="contact ">
      <Form className="formContact" onSubmit={handleOnSubmit}>
        <h2 style={{ textAlign: 'center' }}>
          יש לך שאלה? מלא פרטים וניצור איתך קשר!
        </h2>
        <Row>
          <Col>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
              <Form.Label>שם פרטי</Form.Label>
              <Form.Control
                size="sm"
                type="text"
                name="firstname"
                id="firstname"
                onChange={(e) => setFirstName(e.target.value)}
              />
            </Form.Group>
          </Col>
          <Col>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
              <Form.Label>שם משפחה</Form.Label>
              <Form.Control
                size="sm"
                type="text"
                name="lastname"
                id="lasstname"
                onChange={(e) => setLastName(e.target.value)}
              />
            </Form.Group>
          </Col>
        </Row>
        <Row>
          <Col>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
              <Form.Label>מספר טלפון</Form.Label>
              <Form.Control
                size="sm"
                type="text"
                name="phonenumber"
                id="phonenumber"
                onChange={(e) => setPhoneNumber(e.target.value)}
              />
            </Form.Group>
          </Col>
          <Col>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
              <Form.Label>דואר אלקטרוני</Form.Label>
              <Form.Control
                size="sm"
                type="text"
                placeholder="name@example.com"
                name="email"
                id="email"
                onChange={(e) => setEmail(e.target.value)}
              />
            </Form.Group>
          </Col>
        </Row>

        <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">
          <Form.Label>הקלד את השאלה</Form.Label>
          <Form.Control as="textarea" name="message" id="message" rows={3} />
        </Form.Group>

        {/*         {['checkbox'].map((type) => (
          <div key={type} className="mb-3">
            <Form.Check type={type} id={`check-api-${type}`}>
              <Form.Check.Input id="checkForPost" type={type} isValid />
              <Form.Check.Label
                style={{ color: 'white' }}
              >{`אישור דיוור`}</Form.Check.Label>
              <Form.Control.Feedback type="valid"></Form.Control.Feedback>
            </Form.Check>
          </div>
        ))}
 */}
        <Button variant="primary" type="submit" onclick={contactValidation}>
          שלח
        </Button>
      </Form>
      <div className="details">
        <img className="d-block w-100 imgContact" src={image1} />
        <p className="textOnImage">OR COHAVI - PERSONAL TRAINER</p>
        <p className="textOnImageBottom">אור כוכבי - מאמן אישי</p>
      </div>
    </div>
  )
}
